// theme
$c-bg: #111;
$c-font-base: #ddd;

// normalize
html,
body {
  width: 100%;
  height: 100%;
  background: $c-bg;
}
* {
  margin: 0;
  padding: 0;
  outline: none;
}

a {
  text-decoration: none;
}

@mixin neo($color) {
  background-color: $color;
  box-shadow: 3px 3px 10px 2px darken($color, 30%),
    -3px -2px 10px 1px lighten($color, 10%);
  transition: 300ms box-shadow ease-out;

  &:hover {
    box-shadow: 3px 3px 8px darken($color, 30%),
      -3px -3px 8px lighten($color, 10%), inset 0 0 5px lighten($color, 2%);
  }
  &:active {
    box-shadow: inset 2px 2px 3px darken($color, 20%),
      inset -2px -2px 3px lighten($color, 3%);
  }
}
